* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

.slider {
  position: relative;
  width: 60%;
  margin: auto;
  overflow: hidden;
}

.slider__container {
  display: flex;
  width: 100%;
  height: 500px;
  transform: translate(0);
  transition: transform 0.8s ease;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  z-index: 10;
  font-size: 20px;
  cursor: pointer;
}

.button--prev {
  left: 3%;
}
.button--next {
  right: 3%;
}

.pagers {
  position: absolute;
  bottom: 20px;
  width: 100%;
  height: 20px;
  display: flex;
  justify-content: center;
}

.pager {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 50%;
  width: 12px;
  height: 12px;
  margin: 0 5px;
  transition: all 0.3s ease;
}

.pager:hover {
  background-color: rgb(255, 165, 0);
}

.pager.active {
  background-color: rgba(100, 100, 100, 0.75);
  background-color: orange;
}
